.trainers__container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
}

.card.trainer {
    padding: 1rem;
}

.trainer__img {
    border-radius: 2rem 0rem;
    overflow: hidden;
}

.trainer {
    & p {
        margin-top: 0.5rem;
    }
    
    & h3 {
        margin-top: 2rem;
    }
    
    &__socials {
        display: flex;
        gap: 1rem;
        justify-content: center;
        align-items: center;
        margin: 2rem 0;

        & a {
            display: flex;
            justify-content: center;
            font-size: 0.7rem;
            background: var(--color-gray-600);
            padding: 0.8rem;
            border-radius: 0.7rem;
            transition: var(--transition);
        }
    }

    &:hover a {
        background: var(--color-gray-500);
    }
}

/* ================ MEDIA QUERIES 1024px ================= */

@media screen and (max-width: 1024px) {
    .trainers__container  {
        grid-template-columns: 1fr 1fr;
        gap: 2.5rem;
    }
}

/* ================ MEDIA QUERIES 600px ================= */

@media screen and (max-width: 600px) {
    .trainers__container  {
        grid-template-columns: 1fr;
        gap: 2rem;
    }

    .card.trainer {
        width: 90%;
        margin-inline: auto;
    }
}
