/*================ MAIN HEADER ================*/

.main__header {
    width: 100vw;
    height: calc(100vh - 3rem);
    display: grid;
    place-items: center;
    margin-top: 3rem;

    &-container {
        height: 100%;
        display: grid;
        grid-template-columns: 1fr 1fr;
        align-items: center;
        justify-content: space-between;
        gap: 6rem;
    }

    &-left {
        margin-top: -3rem;

        h4 {
            margin-bottom: 1rem;
            color: var(--color-secondary);
            font-weight: 400;
        }

        p {
            margin: 1rem 0 3.5rem;
            font-size: 1.1rem;
        }
    }
    
    &-right {
        display: grid;
        place-items: center;
        position: relative;
    }

    &-circle {
        width: 25rem;
        height: 25rem;
        background: linear-gradient(75deg, var(--color-secondary), transparent);
        border-radius: 50%;
        position: absolute;
    }

    &-image {
        position: relative;
    }

    &-right:hover &-circle {
        background: linear-gradient(75deg, rgb(227, 158, 55), transparent);
    }
    
}

