/*================ PROGRAMS ================*/

.section__head {
    display: flex;
    align-items: center;
    gap: 1rem;

    span {
        background: var(--color-gray-500);
        padding: 0.8rem;
        border-radius: 1rem;
        color: var(--color-secondary);
        font-size: 1.5rem;
    }
}

.card {
    background: var(--color-gray-500);
    border: 2px solid transparent;
    text-align: center;
    padding: 3rem 2rem;
    border-radius: 3rem 0 3rem 0;
    transition: var(--trnsition);
    
    &:hover {
        background: transparent;
        border-color: var(--color-gray-400);
        cursor: default;
    }

    & span {
        width: 3rem;
        height: 3rem;
        background: var(--color-primary);
        color: var(--color-gray-100);
        font-size: 1.5rem;
        padding: 0.8rem;
        display: grid;
        place-items: center;
        margin-bottom: 1.5rem;
        margin-inline: auto;
        border-radius: 1rem;
        transition: var(--trnsition);
    }

    &:hover span {
        background: var(--color-secondary);
        color: var(--color-gray-600);
    }

    & small {
        margin-top: 1rem;
        display: block;
        font-weight: 300;
    }
}

.programs {
    margin-top: 4rem;
}

.progams__wrapper {
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 3rem;
    margin-top: 4rem;
}

.programs__program a {
    margin-top: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover a {
        background: var(--color-secondary);
        color: var(--color-gray-600);
    }
}

/*================ VALUES ================*/

.values__container {
    display: grid;
    grid-template-columns: 38% 50%;
    gap: 12%;
}

.values__image {
    filter: saturate(0.25);
    transition: var(--trnsition);

    &:hover {
        filter: saturate(1);
    }
}

.values__right > p {
    margin: 1.5rem 0 5rem;
}

.values__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 3.4rem 2.4rem;
}

.card.values__value {
    padding-top: 3.5rem;
    text-align: left;
    position: relative;

    & span {
        position: absolute;
        top: -1.5rem;
    }
}

/*================ QUESTION ================*/

.faq {
    background: var(--color-gray-500);
    padding: 2rem;
    border-radius: 1rem;
    cursor: pointer;

    & div {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__icon {
        background:  transparent;
        color: var(--color-gray-100);
        font-size: 1.5rem;
    }

    & p {
        margin-top: 1.5rem;
    }
}

.ques__wrapper {
    margin-top: 4rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.5rem 5rem;
    align-items: flex-start;
}

/*================ COMMENTS ================*/

.comments {
    width: 50rem;
    margin-inline: auto;
    margin-top: 10rem;

    &__head {
        justify-content: center;
    }
}

.card.comment {
    position: relative;
    text-align: left;
    margin-top: 5rem;
}

.comment {
    &__avatar {
        width: 4rem;
        height: 4rem;
        border-radius: 1.5rem;
        border: 2px solid transparent;
        overflow: hidden;
        position: absolute;
        top: -2rem;
        left: calc(50% - 2rem);
        box-shadow: 0 1.5rem 2rem rgba(26, 26, 54, 0.4);
        transition: var(--trnsition);
    }

    &:hover &__avatar {
        border-radius: 50%;
        border-color: var(--color-gray-400);
    }

    &__quote {
        font-style: italic;
        margin: 1rem 0 2rem;
    }
}

.card small.comment__title {
    margin-top: 0.3rem;
}

.comments__btn-container {
    width: fit-content;
    margin: 2.5rem auto;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;

    .comments__btn {
        background: transparent;
        cursor: pointer;

        & svg {
            fill: var(--color-primary);
            font-size: 1.8rem;
            height: 3rem;
            width: 3rem;
        }
    }
}

/*================ FOOTER ================*/

footer {
    background: var(--color-primary);
    margin-top: 7rem;
    padding-top: 7rem;
    font-size: 0.9rem;
    color: var(--color-gray-100);
}

.footer__conainer {
    display: grid;
    grid-template-columns: 26rem 1fr 1fr 1fr;
    gap: 6rem;

    & article {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 0.7rem;

        & p {
            margin-top: 0.5rem;
        }

        & h4 {
            margin-bottom: 0.6rem;
            font-size: 1rem;
        }
    }
}

.footer__socials {
    margin-top: 1.5rem;
    display: flex;
    gap: 1rem;
    align-items: center;

    & a {
        display: flex;
        background: var(--color-gray-100);
        padding: 0.7rem;
        border: 1px solid transparent;
        border-radius: 0.6rem;
        transition: var(--trnsition);
    }

    & a svg {
        color: var(--color-gray-600);
    }

    & a:hover {
        border-color: var(--color-gray-100);
        background: transparent;
    }
}

.footer__copyright {
    color: var(--color-gray-100);
    text-align: center;
    padding: 1.5rem 0;
    border-top: 2px solid var(--color-primary-variant);
    margin-top: 5rem;
}

/* ================ MEDIA QUERIES 1024px ================= */

@media screen and (max-width: 1024px) {
    /* MAIN HEADER */
    .main__header {
        height: fit-content;
        padding: 12rem 0;
    }

    .main__header-container {
        gap: 0;
    }

    .main__header-circle {
        width: 16rem;
        height: 16rem;
    }

    /* PROGRAMS */
    .progams__wrapper {
        grid-template-columns: 1fr 1fr;
    }

    /* VALUES */
    .values__container {
        grid-template-columns: 1fr;
        justify-content: center;
        gap: 4rem;
    }

    .values__wrapper {
        gap: 4rem 3rem;
    }

    .values__image {
        display: none;
    }

    /* FAQs */
    .ques__wrapper {
        grid-template-columns: 1fr;
    }

    /* COMMENTS */
    .comments {
        width: 75%;
    }

    /* FOOTER */
    .footer__conainer {
        grid-template-columns: 1fr 1fr;
        gap: 4rem;
    }
}

/* ================ MEDIA QUERIES 600px ================= */

@media screen and (max-width: 600px) {
    /* MAIN HEADER */
    .main__header {
        margin-top: 0;
        padding: 12rem 0 0;
        height: 100vh;
    }

    .main__header-image, .main__header-circle {
        display: none;
    }

    .main__header-container {
        grid-template-columns: 1fr;
    }

    /* PROGRAMS */
    .progams__wrapper {
        grid-template-columns: 1fr;
        gap: 2rem;
        margin-top: 3rem;
    }

    .programs__program {
        width: 84%;
        margin: 0 auto;
    }

    /* VALUES */
    .values__wrapper {
        grid-template-columns: 1fr;
        gap: 3rem;
    }

    .card.values__value {
        width: 84%;
        margin-inline: auto;
    }

    /* FAQS */
    .ques__wrapper {
        gap: 1rem;
        margin-top: 3rem;
    }

    /* COMMENTS */
    .comments {
        width: 100%;
    }

    /* FOOTER */
    footer {
        margin-top: 7rem;
    }

    .footer__conainer {
        grid-template-columns: 1fr;
        gap: 3rem;
    }

    .footer__conainer article {
        align-items: center;
    }

    .footer__conainer article p {
        text-align: center;
    }
}



